body {
  background-color: #cecbcd;
  background-repeat: no-repeat;
  background-size: cover;
}

.clock {
  width: 350px;
  height: 350px;
  background-color: rgba(255, 255, 255, 0);
  background-image: url("https://i.ibb.co/dBGxSK1/hiclipart-com-3.png");
  background-size: cover;
  border-radius: 50%;
  position: relative;
  left: 400px;
}

.clock-arrow {
  transform-origin: bottom;
  background-color: #000;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  position: absolute;
  transform: translateX(-50%);
}

.clock-center {
  width: 20px;
  height: 20px;
  background-color: #000;
  border-radius: 50%;
  position: absolute;
  top: 165px;
  left: 167px;
}

.clock-seconds__arrow {
  width: 3px;
  height: 35%;
  bottom: 50%;
  left: 50%;
}

.clock-minutes__arrow {
  width: 5px;
  height: 35%;
  bottom: 50%;
  left: 50%;
}

.clock-hours__arrow {
  width: 8px;
  height: 24%;
  bottom: 50%;
  left: 50%;
}

.date {
  width: 100px;
  height: 100px;
  color: #000;
}

.calendar {
  justify-content: space-evenly;
  display: flex;
}

.date-style {
  text-shadow: 3px 3px 2px #cecbcd;
  font-size: 20px;
  font-weight: 700;
  display: flex;
}

.digital-clock {
  text-align: center;
  text-shadow: 3px 3px 2px #cecbcd;
  font-size: 40px;
}

.box {
  width: 400px;
  height: 200px;
  background-color: #fbb9c4;
  border: 1px solid #000;
  margin: 0 auto;
  padding: 20px;
  display: none;
}

.item {
  width: 100px;
  height: 100px;
  text-align: center;
  vertical-align: middle;
  border: 1px solid #000;
  outline: auto;
  line-height: 100px;
}

.js-container {
  width: 303px;
  border: 1px solid #000;
  display: flex;
}

